$icon-action-x: 0px;
$icon-action-y: 130px;
$icon-action-offset-x: 0px;
$icon-action-offset-y: -130px;
$icon-action-width: 28px;
$icon-action-height: 93px;
$icon-action-total-width: 44px;
$icon-action-total-height: 223px;
$icon-action-image: 'sprite.png';
$icon-action: (0px, 130px, 0px, -130px, 28px, 93px, 44px, 223px, 'sprite.png', 'icon-action', );
$icon-cart-hover-name: 'icon-cart-hover';
$icon-cart-hover-x: 0px;
$icon-cart-hover-y: 42px;
