$icon-cart-hover-width: 44px;
$icon-cart-hover-height: 44px;
$icon-cart-hover-total-width: 44px;
$icon-cart-hover-total-height: 223px;
$icon-cart-hover-image: 'sprite.png';
$icon-cart-hover: (0px, 42px, 0px, -42px, 44px, 44px, 44px, 223px, 'sprite.png', 'icon-cart-hover', );
$icon-cart-name: 'icon-cart';
$icon-cart-x: 0px;
$icon-cart-y: 86px;
$icon-cart-offset-x: 0px;
$icon-cart-offset-y: -86px;
$icon-cart-width: 44px;
$icon-cart-height: 44px;
$icon-cart-total-width: 44px;
$icon-cart-total-height: 223px;
$icon-cart-image: 'sprite.png';
$icon-cart: (0px, 86px, 0px, -86px, 44px, 44px, 44px, 223px, 'sprite.png', 'icon-cart', );
$img-crest-name: 'img-crest';
$img-crest-x: 0px;
$img-crest-y: 0px;
$img-crest-offset-x: 0px;
$img-crest-offset-y: 0px;
$img-crest-width: 17px;
