* {
  box-sizing: border-box; }

// Selection colors (easy to forget)
// ::selection
//   background: $selection-color
// ::-moz-selection
//   background: $selection-color
// img::selection
//   background: transparent
// img::-moz-selection
//   background: transparent

body {
 }  // -webkit-tap-highlight-color: $selection-color

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: $color-white;
  font-size: rem(14);
  font-family: $font-roboto;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $color-dark;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 15px 0;
  font-family: $font-intro;
  text-transform: uppercase;
  display: block;
  small {
    font-size: rem(12);
    display: inline-block;
    margin-left: 10px;
    color: #959595;
    font-weight: 400;
    font-family: $font-roboto;
    text-transform: none; } }

.h1, h1 {
  font-size: rem(33);
  line-height: 1.05;
  letter-spacing: -1px;
  color: $color-blue;
  margin-bottom: 25px; }

.h2, h2 {
  font-size: rem(28);
  line-height: 1.14;
  margin-bottom: 20px;
  &.line {
    padding-left: 54px;
    position: relative;
    &::before {
      position: absolute;
      left: 0;
      top: 15px;
      height: 1px;
      width: 40px;
      background-color: $color-dark;
      content: ''; } } }

.h3, h3 {
  font-size: rem(22);
  line-height: 1.38;
  &.line {
    padding-left: 50px;
    position: relative;
    &::before {
      position: absolute;
      left: 0;
      top: 14px;
      height: 1px;
      width: 40px;
      background-color: $color-dark;
      content: ''; } } }

.h4, h4 {
  font-size: rem(20);
  line-height: 1.38; }

.h5, h5 {
  font-size: rem(18); }

.h6, h6 {
  font-size: rem(16); }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  margin: 0 0 10px 0;
  line-height: 1.7;
  &+h2, &+.h2, &+h3, &+.h3, &+h4, &+.h4, &+h5, &+.h5, &+h6, &+.h6 {
    margin-top: 20px; } }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 400;
  font-family: $font-roboto-bold; }

hr {
  border: solid $color-gray;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 25px;
  height: 0; }

// Links
a {
  color: $link-color;
  text-decoration: none;
  outline: none;
  transition: color 0.35s, background-color 0.35s, border-color 0.35s;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: underline;
    outline: none; } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px; }

/* text align */
.text {
  &-center, &--center {
    text-align: center; }
  &-left, &--left {
    text-align: left; }
  &-right, &--right {
    text-align: right; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;

  &__block {
    background: $color-blue;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-blue;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transition: transform(-50%,-50%);
    &:before, &:after {
      background: $color-blue;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }

  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }

    40% {
      box-shadow: 0 -2em;
      height: 5em; } }


  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }

    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }

@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }

  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }


@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }

  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; }

  &-thumbnails, &--thumbnails {
    border: 6px solid $color-gray; } }

/* section */
.section {
  padding: 50px 0;
  min-width: 320px; }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

/* bg */
.bg {
  &--pink {
    background-color: pink !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--padding {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--margin {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } } }

/* icons */
.icon {
  display: inline-block;
  // &--sprite
  //   @include sprite($img-name)
  &--cart {
    @include sprite($icon-cart); }
  &--cart-hover {
    @include sprite($icon-cart-hover); }
  &--action {
    @include sprite($icon-action); } }

.content {
  padding-bottom: 30px; }

.banner {
  margin-bottom: 25px; }

.w100per {
  width: 100% !important; }
.nowrap {
  white-space: nowrap !important; }

.color {
  &--blue {
    color: $color-blue !important; }
  &--gray {
    color: $color-gray !important; }
  &--white {
    color: $color-white !important; } }

