/* header */
.header {
  margin-bottom: 32px;
  &__logo {
    position: relative;
    display: inline-block;
    float: left;
    img {
      display: block;
      width: 252px;
      height: 50px; }
    &>a {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 10; } }

  &__top {
    border-bottom: 1px solid #eff3f8;
    padding: 17px 0 0 0;
    height: 84px; }

  &__bottom {
    height: 89px; }

  &__menu {
    background-color: #1c1c1f;
    height: 40px;
    position: relative;
    &__background {
      display: none; } }

  &__phone {
    font-size: rem(20);
    float: left;
    margin-left: 47px;
    padding-top: 7px;
    width: 160px;
    strong {
      display: block;
      font-family: $font-intro;
      line-height: 1;
      margin-bottom: 1px;
      a {
        text-decoration: none;
        color: $color-dark; } }
    small {
      display: block;
      font-size: rem(11);
      a {
        text-decoration: none;
        display: inline-block;
        border-bottom: 1px dashed $color-blue;
        &:hover {
          border-bottom-color: transparent; } } } }

  &__time {
    margin-left: 66px;
    float: left;
    padding-top: 7px;
    font-size: rem(20);
    strong {
      display: block;
      font-family: $font-intro;
      line-height: 1;
      margin-bottom: 1px; }
    small {
      display: block;
      font-size: rem(11);
      color: #9b9b9b; } }

  &__cart {
    position: relative;
    float: right;
    padding-left: 55px;
    min-height: 44px;
    min-width: 167px;
    padding-top: 5px;
    margin-top: 4px;
    &>a {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 10; }
    &__icon {
      position: absolute;
      left: 0;
      top: 0;
      width: 44px;
      height: 44px;
      i {
 }        // transition: background 0.25s
      span {
        position: absolute;
        width: 19px;
        height: 19px;
        border-radius: 50%;
        overflow: hidden;
        background: $color-dark;
        color: $color-white;
        font-size: rem(9);
        text-align: center;
        line-height: 20px;
        right: -3px;
        top: -3px;
        transition: all 0.35s; } }
    strong {
      font-size: rem(20);
      display: block;
      font-family: $font-intro;
      line-height: 1;
      margin-bottom: 3px;
      color: $color-dark;
      transition: color 0.35s; }
    small {
      display: block;
      font-size: rem(11);
      color: #9b9b9b;
      i {
        vertical-align: 0px; } }
    &:hover {
      .icon--cart {
        @include sprite($icon-cart-hover); }
      .header__cart__icon {
        span {
          background: $color-blue; } }
      strong {
        color: $color-blue; } } }

  &__text {
    float: left;
    font-family: $font-intro;
    text-transform: uppercase;
    margin-left: 30px;
    margin-top: 22px;
    strong {
      font-size: rem(25);
      display: block;
      line-height: 1;
      margin-bottom: 3px;
      color: $color-dark;
      font-family: $font-intro; }
    small {
      display: block;
      font-size: rem(15);
      color: #767779;
      letter-spacing: 2.5px; } }

  &__search {
    float: right;
    margin-right: 30px;
    width: 512px;
    margin-top: 26px;
    position: relative;
    &.is-search {
      .header__search__input {
        border-color: $color-blue;
        background-color: $color-white; }
      .header__fast-search {
        // opacity: 1
        // visibility: visible
        display: block; } }

    form {
      position: relative;
      @include clearfix; }
    &__input {
      width: calc(100% - 37px);
      border: 1px solid transparent;
      background: #eff3f8;
      height: 37px;
      padding: 0 15px;
      font-size: rem(12);
      font-family: $font-roboto;
      color: $primary-color;
      transition: border 0.35s;
      position: absolute;
      left: 0;
      top: 0; }
    &__btn {
      width: 37px;
      height: 37px;
      background-color: $color-blue;
      border: 0;
      position: relative;
      color: $color-white;
      float: right;
      font-size: rem(20);
      i {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%); }
      &:hover {
        background-color: darken($color-blue, 10%); } }
    &__cap {
      position: absolute;
      z-index: 10;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      // background: pink
      display: none; } }

  &__fast-search {
    position: absolute;
    top: 36px;
    right: 0;
    left: 0;
    background: $color-white;
    z-index: 150;
    border: 1px solid $color-blue;
    // opacity: 0
    // visibility: hidden
    // transition: opacity 0.35s
    overflow: hidden;
    display: none; } }

.fast-search {
  &__title {
    display: block;
    color: $color-dark;
    text-transform: uppercase;
    font-size: rem(12);
    font-family: $font-intro;
    line-height: 1;
    margin-bottom: 2px; }
  &__price {
    color: $color-blue;
    font-size: rem(23);
    font-family: $font-intro;
    .icon-rouble {
      font-size: rem(21);
      vertical-align: middle; } }
  // padding: 10px 0 0 0
  padding: 0;
  margin: 0;
  li {
    list-style: none;
    margin: 0;
    a {
      display: block;
      text-decoration: none;
      padding: 15px 55px 10px 102px;
      position: relative;
      min-height: 68px;
      &>img {
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%); }
      &:hover {
        background-color: #eff3f8; }
      &::after {
        font-size: rem(14);
        position: absolute;
        right: 26px;
        top: 50%;
        transform: translateY(-50%);
        color: $color-blue;
        content: "\f125";
        font-family: $font-ionicons; } }
    &.last {
      // margin-top: 10px
      a {
        height: 52px;
        min-height: inherit;
        line-height: 52px;
        padding: 0 55px 0 30px;
        border-top: 1px solid #eff3f8;
        font-size: rem(14); } } } }
